@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Rubik+Mono+One");
@import url("https://fonts.googleapis.com/css?family=Bad+Script");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "iconfont"; src: url("../fonts/iconfont.eot"); src: url("../fonts/iconfont.eot?#iefix") format("eot"), url("../fonts/iconfont.woff2") format("woff2"), url("../fonts/iconfont.woff") format("woff"), url("../fonts/iconfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }

.icon { font-family: "iconfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon { display: inline-block; }

.icon-adress:before { content: ""; }

.icon-facebook:before { content: ""; }

.icon-instagram:before { content: ""; }

.icon-next:before { content: ""; }

.icon-waves:before { content: ""; }

.icon-world:before { content: ""; }

.icon-wreath:before { content: ""; }

.icon-adress { width: 1em; height: 1em; }

.icon-facebook { width: 1em; height: 1em; }

.icon-instagram { width: 1em; height: 1em; }

.icon-next { width: 1em; height: 1em; }

.icon-waves { width: 1em; height: 1em; }

.icon-world { width: 1em; height: 1em; }

.icon-wreath { width: 1em; height: 1em; }

body { background: #FFF; font-size: 16px; line-height: 1.4; }

.block { display: flex; }

.archive { margin-top: 40px; }

.archive__sub-header { padding: 40px 0px 10px; }

.donation { color: #000000; text-decoration: none; background-color: #ffffff; padding: 5px 10px; border-radius: 5px; }

.main { display: flex; width: 60%; min-height: 100%; justify-content: center; align-items: center; text-align: center; color: #ffffff; flex-direction: column; margin-top: 80px; margin-bottom: 80px; }

@media only screen and (max-width: 1023px) { .main { width: 100%; } }

.main__header { display: block; }

@media only screen and (max-width: 1023px) { .main__header { font-size: 1.6em; } }

.main__donation { display: block; padding: 20px 0px; }

.social-buttons { display: flex; }

.social-buttons__item { margin: 20px; color: #ffffff; }

@media only screen and (max-width: 1023px) { .social-buttons__item { margin: 10px; } }

.social-buttons__img { width: 50px; margin: auto; }

@media only screen and (max-width: 1023px) { .social-buttons__img { width: 30px; } }

.social-buttons__link { display: flex; text-decoration: none; color: #ffffff; font-family: 'Bad Script', cursive; flex-direction: column; text-align: center; text-transform: uppercase; }

.social-buttons__text { padding-top: 10px; font-size: 0.8em; }

@media only screen and (max-width: 1023px) { .social-buttons__text { font-size: 0.6em; } }

.t-shirt { margin-top: 40px; }

.t-shirt__link { display: block; color: #ffffff; text-decoration: none; font-family: 'Bad Script', cursive; padding: 10px; }

.youtube { position: relative; margin: auto; overflow: hidden; }

html { position: relative; font-size: 100%; width: 100vw; height: 100%; overflow-x: hidden; }

@media only screen and (max-width: 1440px) { html { font-size: 100%; } }

@media only screen and (max-width: 1200px) { html { font-size: 90%; } }

@media only screen and (max-width: 1024px) { html { font-size: 80%; } }

@media only screen and (max-width: 640px) { html { font-size: 60%; } }

body { font-family: "Rubik Mono One", sans-serif; width: 100vw; height: 100%; position: relative; background-image: url("../img/background.jpg"); background-repeat: none; background-position: center; background-size: cover; background-attachment: fixed; }
