@font-face {
  font-family: "iconfont";
  src: url('../fonts/iconfont.eot');
  src: url('../fonts/iconfont.eot?#iefix') format('eot'),
    url('../fonts/iconfont.woff2') format('woff2'),
    url('../fonts/iconfont.woff') format('woff'),
    url('../fonts/iconfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

$icon-adress: "\EA01";
$icon-facebook: "\EA02";
$icon-instagram: "\EA03";
$icon-next: "\EA04";
$icon-waves: "\EA05";
$icon-world: "\EA06";
$icon-wreath: "\EA07";


%icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  @extend %icon;
  display: inline-block;
}

.icon-adress:before { content: $icon-adress; }
.icon-facebook:before { content: $icon-facebook; }
.icon-instagram:before { content: $icon-instagram; }
.icon-next:before { content: $icon-next; }
.icon-waves:before { content: $icon-waves; }
.icon-world:before { content: $icon-world; }
.icon-wreath:before { content: $icon-wreath; }
