@import "helpers/all";
@import "generated/iconfont";
@import "generated/sprite-svg";
// @import "generated/sprite-png";
@import "common";
@import "layouts";
@import "main";
@import "variables";


.archive {

    margin-top: 40px;



	&__header {



    }



    &__sub-header {

        padding: 40px 0px 10px;

    }

}




.donation {

    color: $black;

    text-decoration: none;

    background-color: $white;

    padding: 5px 10px;

    border-radius: 5px;

}




.main {

    display: flex;

    width: 60%;

    min-height: 100%;

    justify-content: center;

    align-items: center;

    text-align: center;

    color: $white;

    flex-direction: column;

    margin-top: 80px;

    margin-bottom: 80px;



    @include r(1023) {

        width: 100%;

    }



    &__header {

        display: block;



        @include r(1023) {

            font-size: 1.6em;

        }

    }



    &__donation {

        display: block;

        padding: 20px 0px;

    }

}




.social-buttons {

    display: flex;



    &__item {

        margin: 20px;

        color: $white;



        @include r(1023) {

            margin: 10px;

        }

    }



    &__img {

        width: 50px;

        margin: auto;



        @include r(1023) {

            width: 30px;

        }

    }



    &__link {

        display: flex;

        text-decoration: none;

        color: $white;

        font-family: 'Bad Script', cursive;

        flex-direction: column;

        text-align: center;

        text-transform: uppercase;

    }



    &__text {

        padding-top: 10px;

        font-size: 0.8em;



         @include r(1023) {

            font-size: 0.6em;

        }

    }

}




.t-shirt {

    margin-top: 40px;



    &__link {

        display: block;

        color: $white;

        text-decoration: none;

        font-family: 'Bad Script', cursive;

        padding: 10px;

    }

}




.youtube {

	position:relative;

	margin: auto;

	overflow:hidden;

}



html {
    position: relative;
    font-size: 100%;
    width: 100vw;
    height: 100%;
    overflow-x: hidden;

    @include r(1440) {
        font-size: 100%;
    }

    @include r(1200) {
        font-size: 90%;
    }

    @include r(1024) {
        font-size: 80%;
    }

    @include r(640) {
        font-size: 60%;
    }

}

body {
    font-family: $font;
    width: 100vw;
    height: 100%;
    position: relative;
    background-image: url('../img/background.jpg');
    background-repeat: none;
    background-position: center;
    background-size: cover;
     background-attachment: fixed;
}
